.default_button {
  color: #ffffff;
  font-family: sans-serif;
  font-size: 18px;
  font-weight: bold;
  width: 100%;
  height: 60px;
  border-radius: 36px;
  border: solid 3px #ffffff;
  background-color: #ffffff00;;
}

.default_button:hover {
  opacity: 0.7;
  cursor: pointer;
  outline: none;
}

