body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu_main {
  width: 100%;
  height: 116px;
  box-sizing: border-box;
  padding: 30px 39px 30px 37px;
  background-color: #7ed321;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.menu_list_items {
  font-family: sans-serif;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  color: #ffffff;
  text-decoration: none;
}

.menu_list_items:hover {
  border-bottom: 1px solid #ffffff;
}

.menu_list .menu_list_items {
  padding: 4px 0;
  margin: 0 23px;
}

.menu_list a:nth-child(3) {
  margin: 0 25px 0 22px; 
}

.menu_list a:last-child {
  margin: 0 0 0 24px; 
}

@media screen and (max-width: 739px) {
  .menu_list{
    display: none;
  }
}

@media screen and (max-width: 376px) {
  .menu_main {
    height: 77px;
    padding: 20px 25px;
  }
  .menu_logo {
    height: 37px;
  }
  .menu_list{
    display: none;
  }
}
.default_button {
  color: #ffffff;
  font-family: sans-serif;
  font-size: 18px;
  font-weight: bold;
  width: 100%;
  height: 60px;
  border-radius: 36px;
  border: solid 3px #ffffff;
  background-color: #ffffff00;;
}

.default_button:hover {
  opacity: 0.7;
  cursor: pointer;
  outline: none;
}


.auth_main {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.auth_wrapper_form {
  height: calc(100% - 116px);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 116px;
  box-sizing: border-box;
}

.auth_form {
  width: 503px;
  height: 379px;
  border-radius: 44px;
  background-color: #e990f9;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  padding: 77px 30px 36px;
}

.custom_button {
  width: 151px;
}

.auth_img_bear_mobile {
  display: none;
}

@media screen and (max-width: 376px) {
  .auth_form  {
    width: 100%;
    height: 100%;
    border-radius: none;
    background-color: #ffffff00;
    padding: 0;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .auth_wrapper_form {
    display: -webkit-flex;
    display: flex;
    padding-top: 5px;
    padding-bottom: 0px;
    height: calc(100% - 77px);
  }
  .auth_img_bear g path {
    fill: #e990f9;
  }
  .custom_button {
    color: #e990f9;
    border-color: #e990f9;
  }
  .auth_img_bear {
    display: none;
  }
  .auth_img_bear_mobile {
    display: block;
    margin-bottom: 103px;
  }
}

.default_input {
  width: 100px;
  height: 31px;
  border-radius: 11px;
  border: solid 1px #979797;
  background-color: #ffffff;
  box-sizing: border-box;
  padding: 0 10px;
}

.default_input:focus {
  outline: none;
}

@media screen and (max-width: 376px) {
  .default_input {
    width: 72px;
  }
}
.information_bar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 34px 0;
}

.information_bar_icon {
  margin-right: 46px;
  transition: 0.5s;
}

.information_bar_icon:hover { 
  -webkit-transform: scale(1.1); 
          transform: scale(1.1);
  transition: 0.5s;
}

.information_wrapper_fields {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.information_wrapper_fields span {
  font-family: sans-serif;
  font-size: 14px;
  font-weight: 500;
  opacity: 0.8;
  margin-bottom: 12px;
}

.information_wrapper_fields span:last-child {
  margin-bottom: 0px;
}

.information_date {
  margin-bottom: 9px;
  opacity: 0.7;
}

@media screen and (max-width: 376px) {
  .information_bar {
    border-bottom: 1px solid #eaeaea;
  }
}
.home_top_filters {
  height: 60px;
  background-color: #eaeaea;
